/* General App Styles */
.app-container {
  text-align: center;
  padding: 20px;
}

.app-title {
  font-size: 2em;
  margin-bottom: 0.5em;
}

.app-instructions {
  margin-bottom: 2em;
  font-size: 1em;
  max-width: 80%;
  margin-left: auto;
  margin-right: auto;
}

/* Game Board Styles */
.game-container {
  display: flex;
  flex-direction: row; /* Updated to row for side-by-side layout */
  align-items: stretch; /* Ensure children fill the container height */
}

.game-board {
  display: flex;
  flex-direction: column;
  font-family: Arial, sans-serif;
  flex-grow: 1; /* Allows flexible growth to fit space */
  margin: 10px;
}

.grid-row {
  display: flex;
}

.grid-cell {
  width: 30px;
  height: 30px;
  border: 1px solid #000;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

/* Input and Icon Styles */
.input {
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 1;
}

.clue-icon {
  position: absolute;
  top: 0;
  right: 0;
  background: none;
  border: none;
  cursor: pointer;
}

/* Cell State Styles */
.unused-cell {
  background-color: darkgrey;
}

.correct-guess .input {
  background-color: lightgreen;
}

/* Clue Display Area Styles */
.clue-display-area {
  flex-grow: 3; /* Allows the clue area to take more space */
  border: 1px solid black;
  padding: 20px;
  margin: 10px;
  overflow: auto;
  align-self: stretch; /* Adjusts to match the parent's height */
}

.clue-display-area img {
  width: 100%; /* Ensures image takes full width of the container */
  height: auto; /* Maintains aspect ratio */
  object-fit: contain; /* Ensures the image fits within the bounds without stretching */
  margin-bottom: 10px; /* Adds space between images if there are multiple */
}

/* Responsive Design Adjustments */
@media (max-width: 768px) {
  .clue-display-area img {
    max-height: 150px;
  }
}

@media (max-width: 480px) {
  .clue-display-area img {
    max-height: 100px;
  }
}

.level-selection {
  margin-bottom: 20px;
}

.level-selection button {
  margin-right: 10px;
  padding: 5px 10px;
}
